import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { SkillPack } from "./MsInterfaces";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Selectors from "./Selectors";
import SkillList from "./SkillList";

function App() {
  const cultureList: SkillPack[] = [
    {
      id: 100,
      name: "Rural",
      standard: ["Athletics", "Brawn", "Drive", "Endurance", "First Aid",
        "Locale", "Perception", "Ride", "Combat Style (culture)"],
      professional: ["Craft (any)", "Mechanics", "Knowledge (any)",
        "Musicianship", "Navigate", "Survival", "Track"]
    }, {
      id: 101,
      name: "Urban",
      standard: ["Conceal", "Deceit", "Drive", "Influence", "Insight",
        "Locale", "Willpower", "Combat Style (culture)"],
      professional: ["Art (any)", "Commerce", "Craft (any)",
        "Courtesy", "Language (any)", "Knowledge (any)",
        "Musicianship", "Streetwise"]
    }, {
      id: 102,
      name: "Orbit",
      standard: ["Conceal", "Deceit", "Pilot", "Influence", "Insight",
        "Locale", "Willpower", "Combat Style (culture)"],
      professional: ["Astrogation", "Electronics", "Knowledge (any)",
        "Language (any)", "Mechanics", "Streetwise"]
    }
  ];
  const careerList: SkillPack[] = [
    {
      id: 200,
      name: "Agent",
      standard: ["Conceal", "Deceit", "Evade", "Insight", "Perception", "Stealth",
        "Combat Style (any)"],
      professional: ["Culture (any)", "Disguise", "Language (any)", "Pilot",
        "Sleight", "Streetwise", "Survival", "Track"]
    }, {
      id: 201,
      name: "Belter",
      standard: ["Athletics", "Brawn", "Endurance", "Locale", "Perception",
        "Willpower"],
      professional: ["Commerce", "Craft (Mining)", "Demolitions", "Engineering",
        "Science (Geology)", "Mechanics",
        "Navigation (Underground)", "Pilot", "Survival"]
    }, {
      id: 202,
      name: "Bounty Hunter",
      standard: ["Athletics", "Endurance", "Evade", "Insight", "Perception",
        "Stealth", "Combat Style (any)", "Combat Style (Gunnery)"],
      professional: ["Bureaucracy", "Commerce", "Culture (any)", "Pilot",
        "Streetwise", "Survival", "Track"]
    }, {
      id: 203,
      name: "Colonist",
      standard: ["Athletics", "Brawn", "Drive", "Endurance", "Locale",
        "Perception", "Ride", "Combat Style (any)"],
      professional: ["Commerce", "Craft (any)", "Knowledge (Agriculture)",
        "Mechanics", "Navigation", "Pilot", "Survival", "Track"]
    }, {
      id: 204,
      name: "Criminal",
      standard: ["Athletics", "Brawn", "Deceit", "Evade", "Insight",
        "Perception", "Stealth", "Combat Style (any)"],
      professional: ["Acting", "Commerce", "Electronics", "Gambling",
        "Lockpicking", "Sleight", "Streetwise"]
    }, {
      id: 205,
      name: "Detective",
      standard: ["Customs", "Drive", "Evade", "Influence", "Insight",
        "Perception", "Stealth", "Combat Style (any)"],
      professional: ["Bureaucracy", "Culture (any)", "Disguise",
        "Knowledge (any)", "Research", "Sleight", "Streetwise"]
    }, {
      id: 206,
      name: "Gambler",
      standard: ["Athletics", "Endurance", "Locale", "Perception", "Willpower"],
      professional: ["Acting", "Bureaucracy", "Commerce", "Courtesy", "Gambling",
        "Research", "Sleight", "Streetwise"]
    }, {
      id: 207,
      name: "Journalist",
      standard: ["Customs", "Deceit", "Influence", "Insight", "Locale",
        "Native Tongue", "Perception"],
      professional: ["Bureaucracy", "Culture (any)", "Language (any)",
        "Knowledge (any)", "Oratory", "Politics", "Research",
        "Streetwise"]
    }, {
      id: 208,
      name: "Engineer",
      standard: ["Brawn", "Culture (any)", "Drive", "Endurance", "Influence",
        "Locale", "Willpower"],
      professional: ["Commerce", "Computers", "Craft (any)", "Engineering",
        "Electronics", "Gambling", "Mechanics", "Streetwise"]
    }
  ];

  const useStyles = makeStyles((theme)=>({
    root: {
      flexGrow:1
    }
  }));
  const classes = useStyles();

  //Understand useState with TypeScript 
  //https://lucasbassetti.com/blog/using-usestate-hook-with-typescript/
  const [cultureSkills,setCultureSkills] = useState<string[]>([]);
  const [careerSkills,setCareerSkills] = useState<string[]>([]);
  const [unifiedSkills,setUnifiedSkills] = useState<string[]>([]);

  const selectorsChange = (cultureId: number, careerId: number) => {
    const culture: SkillPack | undefined = cultureList.find((el) => (el.id === cultureId));
    const career: SkillPack | undefined = careerList.find((el) => (el.id === careerId));
    if (culture!==undefined) {
      setCultureSkills(culture.standard.concat(culture.professional).sort());
    }
    if (career!==undefined) {
      setCareerSkills(career.standard.concat(career.professional).sort());
    }
    
    if (culture===undefined || career===undefined) return;
    const skills = culture.standard
      .concat(culture.professional)
      .concat(career.standard)
      .concat(career.professional).sort();
    const unified = Array.from(new Set(skills));
    
    setUnifiedSkills(unified);
  }

  return (
    <Container maxWidth="lg">
      <h1>M-Space Skills Generator</h1>
      <p>Choose your character&apos;s culture and career to obtain a skill list.</p>
      <Selectors cultures={cultureList} careers={careerList} onChange={selectorsChange} />
      <Grid container spacing={2}>
        <Grid item xs={12} md={4}>
          <p>
            <strong>Culture</strong>
            <br/>
            Distribute 100 points among these skills
          </p>
          { cultureSkills.length>0 && 
          <SkillList skills={cultureSkills}/>
          }
        </Grid>
        <Grid item xs={12} md={4}>
          <p>
            <strong>Career</strong>
            <br/>
            Distribute 100 points among these skills
          </p>
          { careerSkills.length>0 && 
          <SkillList skills={careerSkills}/>
          }
        </Grid>
        <Grid item xs={12} md={4}>
          <p><strong>Unified Skill List</strong><br/><br/></p>
          { unifiedSkills.length>0 &&
          <SkillList skills={unifiedSkills}/>  
          }
        </Grid>
      </Grid>
    </Container>
  );
}

export default App;
